body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

body {
    font-family: bree serif, sans-serif;
    -webkit-font-smoothing: antialiased;
    margin: 0 20px;
}
article {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
}
a {
    color: #000;
    text-decoration: none;
}
a:hover {
    font-weight: 600;
    text-decoration: underline;
}
.post-ads {
    margin: 50px 0;
}
.markdown-body {
    font-size: 18px;
}
.Chinese .markdown-body {
    line-height: 200%;
}
.site-title {
    font-size: 3rem;
    margin-top: 32px;
}
.site-date-catalog {
    font-size: 2rem;
}
.signatures {
    font-family: permanent marker, Impact, Charcoal, sans-serif;
}
.signatures a {
    text-decoration: none;
}
.header-line {
    width: 100%;
    border-width: 3px;
    border-color: #000;
    border-style: solid none none none;
    margin: 18px 0;
}
.lang-switch {
    font-weight: 600;
}
#posts-list {
    min-height: 70%;
}
.posts-date,
.posts-title {
    font-size: 1.2rem;
}
.posts-line {
    margin: 12px 0;
}
.posts-categories {
    font-size: 0.8rem;
    margin: auto;
    text-align: center;
}
.posts-category {
    padding: 3px;
    border: #000 2px solid;
    margin-bottom: 3px;
}
.site-footer {
    margin-top: 50px;
    margin-bottom: 80px;
    display: flex;
    justify-content: flex-end;
    padding: 12px 0;
    border-width: 3px;
    border-style: solid none;
    border-color: #000;
}
.site-footer-item {
    margin-right: 12px;
}
@media screen and (max-width: 600px) {
    .site-header {
        display: none;
    }
    .post-content {
        padding: 0 12px;
    }
    .post-content p {
        letter-spacing: 0.05em;
    }
}
.post-content img {
    max-width: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 12px;
}
.post-header {
    margin-bottom: 50px;
}
.post-title {
    font-size: 2.5rem;
    font-weight: 600;
}
.post-category {
    display: inline;
    font-weight: 900;
    padding: 2px;
    border: #000 2px solid;
}
.post-date {
    font-weight: 800;
    font-style: italic;
}
.post-author {
    float: right;
    font-weight: 600;
}
.post-content {
    margin-bottom: 50px;
}
.post-content p {
    hyphens: auto;
    line-height: 1.8;
    text-justify: ideographic;
    margin-bottom: 1em;
}
.releated-content {
    border-width: 3px;
    border-style: solid;
    border-color: #000;
    padding: 0 10px;
    margin-bottom: 50px;
    margin-top: 100px;
}
.releated-content li {
    margin: 5px 0;
}
.taxonomy-term {
    font-size: 3rem;
}
.gallery-img {
    text-align: center;
}
.gallery-img span {
    text-align: center;
}
.gallery-img-desc {
    font-size: 0.8em;
    font-weight: 800;
}
#disqus_thread {
    position: relative;
}
#disqus_thread:after {
    content: "";
    display: block;
    height: 55px;
    width: 100%;
    position: absolute;
    bottom: 0;
    background: #fff;
}
.sub-header-line {
  width: 100%;
  border-width: 1px;
  border-color: #000;
  border-style: solid none none none;
  margin: 18px 0;
}
